import React, { Component } from "react";
import Pembayaran from "../Images/PembayaranSupplier.svg";
import Tolak from "../Images/TolakSupplier.svg";
import Titipan from "../Images/Titipan.svg";
import { Link } from "react-router-dom";
import Skeleton from "react-loading-skeleton";

class KlasifikasiSupplier extends Component {
  constructor(props) {
    super(props);
    this.state = {
      show: true,
    };
    this.test = this.test.bind(this);
  }

  test() {
    this.setState({
      show: true,
    });
  }

  render() {
    setTimeout(this.test, 1000);
    return (
      <div className="container-fluid" style={{ width: "100%" }}>
        <div className="row col-lg-12">
          <div className="col-lg-4">
            {this.state.show === true ? (
              <div className="card-menu">
                <div className="card-body text-center">
                  <img src={Pembayaran} width="300" alt="Kode Barang" />
                  <h3>
                    Pembayaran Hutang <br /> Supplier
                  </h3>
                  <Link to="/ayugold.com/PembayaranHutangSupplier">
                    <button className="btn btn-primary">Masuk</button>
                  </Link>
                </div>
              </div>
            ) : (
              <Skeleton height={300} width={450} />
            )}
          </div>
          <div className="col-lg-4">
            {this.state.show === true ? (
              <div className="card-menu">
                <div className="card-body text-center">
                  <img src={Tolak} width="300" alt="Kode Barang" />
                  <h3>Tolak Pembayaran Hutang Supplier</h3>
                  <Link to="/ayugold.com/TolakanHutangSupplier">
                    <button className="btn btn-primary">Masuk</button>
                  </Link>
                </div>
              </div>
            ) : (
              <Skeleton height={300} width={450} />
            )}
          </div>
          <div className="col-lg-4">
            {this.state.show === true ? (
              <div className="card-menu">
                <div className="card-body text-center">
                  <img src={Titipan} width="300" alt="Kode Barang" />
                  <h3>
                    Titipan <br />
                    Supplier
                  </h3>
                  <Link to="/ayugold.com/TitipanSupplier">
                    <button className="btn btn-primary">Masuk</button>
                  </Link>
                </div>
              </div>
            ) : (
              <Skeleton height={300} width={450} />
            )}
          </div>
        </div>
      </div>
    );
  }
}

export default KlasifikasiSupplier;
